.precaution {
  padding: get-vw(40px)  0 get-vw(80px);
  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: 'left right'; }
  &__left {
    grid-area: left;
    text-align: center;
    position: relative;
    img {
      height: auto; } }
  .img1 {
    position: absolute;
    top: get-vw(150px);
    left: get-vw(130px);
    width: get-vw(220px); }
  .img2 {
    position: absolute;
    top: get-vw(25px);
    left: get-vw(300px);
    width: get-vw(275px); }
  &__right {
    grid-area: right; } }
