.usage {
  background-color: #7c3fc7;
  position: relative;
  margin-top: 50px;
  &:before {
    content: '';
    position: absolute;
    height: 50px;
    top: -50px;
    width: 100%;
    background-color: #7c3fc7; }
  &:after {
    content: '';
    position: absolute;
    bottom: get-vw(-50px);
    left: 0;
    width: 0;
    height: 0;
    border-bottom: get-vw(50px) solid #f8f9fb;
    border-left: get-vw(1890px) solid #7c3fc7; }
  .container {
    max-width: 1100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto;
    grid-template-areas: "title title title" "left middle right";
    position: relative; }
  .title {
    margin-bottom: get-vw(50px);
    color: #fff;
    grid-area: title;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    @extend %relative;
    &:before {
      content: "";
      position: absolute;
      bottom: get-vw(-52px);
      left: 0;
      width: 50.5vw;
      height: get-vw(2px);
      background-color: rgba(247,247, 247, 0.3); } }
  img {
    width: get-vw(50px); }
  p {
    color: #fff;
    font-weight: 400; }
  .heading {
    font-size: get-vw(24px);
    font-weight: 400;
    color: #fff;
    text-transform: uppercase;
    position: relative;
    margin-bottom: get-vw(45px);
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: get-vw(-25px);
      width: get-vw(65px);
      height: get-vw(3px);
      background-color: #fff; } }
  &__left {
    grid-area: left;
    background-color: #fff;
    padding:  get-vw(35px) 0 get-vw(15px)  get-vw(20px);
    box-shadow: 0 4px 10px 1px rgba(146,148,151,.2);
    position: absolute;
    z-index: 50;
    margin-right: get-vw(20px);
    height: get-vw(370px);

    .heading {
      color: #2c333f;
      &:after {
        background-color: #2db86e; } }
    a {
      color: #2db86e;
      display: flex;
      align-items: center;
      font-size: get-vw(16px);
      padding-top: 0.60417vw; }
    img {
      &:last-child {
        padding-left: get-vw(15px);
        width: get-vw(50px); } }
    p {
      color: #7a818c; } }
  &__middle {
    grid-area: middle;
    padding-top: get-vw(35px);
    margin-left: get-vw(30px);
    img {
      width: get-vw(47px); } }

  &__right {
    grid-area: right;
    padding-top: get-vw(35px); }
  .svg-left {
    position: absolute;
    top: get-vw(15px);
    left: -29.04167vw;
    width: 21.04167vw;
    z-index: 60; }
  .svg-right {
    position: absolute;
    left: 53.375vw;
    top: get-vw(15px);
    width: get-vw(300px);
    z-index: 60; } }



