.advantage {
  background-image: url(../img/img-03.png), linear-gradient(to left, #5ec05e , #2fb86d);
  background-repeat: no-repeat, no-repeat;
  background-position: 100% 0%, 100% 0%;
  padding: get-vw(160px) 0;
  -webkit-clip-path: polygon(0 10%, 100% 0, 100% 90%, 0% 100%);
  clip-path: polygon(0 10%, 100% 0, 100% 90%, 0% 100%);
  overflow: hidden;
  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: 'left right'; }
  .title {
    color: #fff; }
  p {
    color: #fff; }
  &__left {
    grid-area: left;
    position: relative;
    img {
      position: absolute;
      width: 20.39583vw;
      left: -24vw;
      top: 60%;
      transform: translateY(-50%); } }

  &__right {
    grid-area: right; }
  sup {
    cursor: pointer;
    font-weight: 600; } }
