@media screen and (max-width: 1200px) {
  .overview {
    h1 {
      font-size: get-vw(80px); }
    a {
      font-size: get-vw(25px); } } }

@media screen and (max-width: 756px) {
  .overview {
    padding: get-vw(1000px) 0;
    text-align: center;
    .container {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;
      grid-template-areas: 'left left' 'right right'; }
    h1 {
        font-size: get-vw(150px);
        margin-bottom: get-vw(150px); }
    a {
      font-size: get-vw(100px);
      padding: get-vw(50px) get-vw(80px); } } }
