.overview {
  background-image: url(../img/img-fs.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: get-vw(200px) 0;
  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: 'left right'; }
  &__left {
    grid-area: left; }
  &__right {
    grid-area: right;
    img {
     position: relative;
     width: 100%;
     height: auto;
     top: get-vw(200px); } }

  h1 {
    font-size: get-vw(65px);
    font-weight: 300;
    color: #fff;
    @extend %relative; }
  a {
    font-size: get-vw(18px);
    padding: get-vw(15px) get-vw(30px);
    color: #fff;
    &:first-of-type {
      background: linear-gradient(to left, #2db86e, #87c84a);
      border-radius: 5px; } } }
